'use strict';

// MODULES //

import isObject from '@stdlib/assert-is-plain-object' ;
import hasOwnProp from '@stdlib/assert-has-own-property' ;
import { isPrimitive as isString } from '@stdlib/assert-is-string' ;
import { isPrimitive as isBoolean } from '@stdlib/assert-is-boolean' ;
import format from '@stdlib/error-tools-fmtprodmsg' ;


// MAIN //

/**
* Validates function options.
*
* @private
* @param {Object} opts - destination object
* @param {Options} options - function options
* @param {string} [options.dtype] - output array data type
* @param {boolean} [options.endpoint] - boolean indicating whether the `stop` value in the output array
* @returns {(Error|null)} null or an error object
*
* @example
* var opts = {};
* var options = {
*     'endpoint': true
* };
* var err = validate( opts, options );
* if ( err ) {
*     throw err;
* }
*/
function validate( opts, options ) {
	if ( !isObject( options ) ) {
		return new TypeError( format( '00W2h', options ) );
	}
	if ( hasOwnProp( options, 'dtype' ) ) {
		opts.dtype = options.dtype;
		if ( !isString( opts.dtype ) ) {
			return new TypeError( format( '00W2i', 'dtype', opts.dtype ) );
		}
	}
	if ( hasOwnProp( options, 'endpoint' ) ) {
		opts.endpoint = options.endpoint;
		if ( !isBoolean( opts.endpoint ) ) {
			return new TypeError( format( '00W30', 'endpoint', opts.endpoint ) );
		}
	}
	return null;
}


// EXPORTS //

export default validate;
